.App {
  text-align: center;
  color: #000;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.app-container {
	width: 100%;
	padding: 20px;
}
.tablerow.header {
	color: #000;
	font-weight: 600;
	background-color: #fafafa;
	border-bottom: 1px solid #ccc;
	margin: 0px;
	padding: 0px;
}
.tablerow.header>div {
	background-color: transparent;
	border-right: 1px solid #ccc;
	margin: 0px;
	padding: 10px;
}
.tablerow {
	height:fit-content;
}
.tablerow:hover {
	background-color: #fafafa;
}
input {
	margin: 10px;
}
.card-content.data-table {
	height: 60vh;
	background-color: #fff;
}
.table-cont {
	border: 1px solid #ccc;
	border-radius: 10px;
	overflow: hidden;
}
.action-row {
	align-items: center;
}

.wo-actions-cont {
	background-color: #fff;
	border: 1px solid #ccc;
	border-radius: 10px;
	padding: 10px;
	margin-top: 10px;
	margin-bottom: 10px;
}

.to {
	font-size: 30px;
	font-weight: 700;
}

.title-info {
	font-size: x-large;
	font-weight: 700;
	text-align: left;
}

.card-menubar>div {
	color: #fff;
	font-size: larger;
	font-weight: 650;
	line-height: 40px;
	text-align: left;
	text-indent: 10px;
}

#wo-count {
	font-weight: 700;
	font-size: large;
	margin-top: 10px;
	margin-bottom: 10px;
}

.form-info-item {
	display: flex;
	align-items: center;
}

.form-info-title {
	font-size: larger;
}
/*Dark theme*/
.app-container#dark {
	background-color: #222222;
}
.app-container#dark>.action-row>.action-button {
	filter: drop-shadow(0px 0px 4px #080813);
}
.app-container#dark>.wo-actions-cont {
	background-color: #333;
	border: 1px solid #111;
}
.app-container#dark>.wo-actions-cont>.action-row>.action-button {
	filter: drop-shadow(0px 0px 4px #080813);
}
.app-container#dark>.wo-actions-cont>.action-row {
	color: #fff;
}
.app-container#dark>.wo-actions-cont>div {
	color: #fff;
}
.app-container#dark>.wo-actions-cont>.action-row>input,
.app-container#dark>.wo-actions-cont>.action-row>.form-info-item>input {
	background-color: #444;
	border: 1px solid #222;
	color: #fff;
}
.app-container#dark>.wo-actions-cont>.action-row>input:focus,
.app-container#dark>.wo-actions-cont>.action-row>.form-info-item>input:focus {
	background-color: #222;
}

.app-container#dark>.table-cont {
	background-color: #333;
	border: 1px solid #111;
}
.app-container#dark>.table-cont>.tablerow.header {
	background-color: #444;
	color: #fff;
	border-bottom: 1px solid #333;
}
.app-container#dark>.table-cont>.tablerow.header>div {
	border-right: 1px solid #333;
}
.app-container#dark>.table-cont>.data-table {
	background-color: #333;
}
.app-container#dark>.table-cont>.card-menubar {
	background-color: #0b4c85;
}
.app-container#dark>.table-cont>.data-table>.tablerow {
	color: #fff;
}
.app-container#dark>.table-cont>.data-table>.tablerow:hover {
	background-color: #444;
}